// components/ForceGraph.tsx
"use client";

import * as d3 from "d3";
import { useEffect, useLayoutEffect, useRef, useState } from "react";

type Node = { id: string; group?: number; x?: number; y?: number };
type Link = { source: string; target: string };

const nodes: Node[] = [
  { id: "Arrays" },
  { id: "Two Pointers" },
  { id: "Sliding Window" },
];

const links: Link[] = [
  { source: "Arrays", target: "Two Pointers" },
  { source: "Arrays", target: "Sliding Window" },
];

export function ForceGraph() {
  const svgRef = useRef<SVGSVGElement | null>(null);
  const containerRef = useRef<HTMLDivElement | null>(null);
  const [size, setSize] = useState({ width: 0, height: 0 });

  // Measure container
  useLayoutEffect(() => {
    if (!containerRef.current) return;

    const observer = new ResizeObserver((entries) => {
      for (const entry of entries) {
        const { width, height } = entry.contentRect;
        setSize({ width, height });
      }
    });

    observer.observe(containerRef.current);
    return () => observer.disconnect();
  }, []);

  // Create / update simulation when size is known
  useEffect(() => {
    if (!svgRef.current || size.width === 0 || size.height === 0) return;

    const svg = d3.select(svgRef.current);
    svg.selectAll("*").remove(); // clear old content on resize

    svg
      .attr("viewBox", `0 0 ${size.width} ${size.height}`)
      .attr("preserveAspectRatio", "xMidYMid meet");

    const link = svg
      .append("g")
      .attr("stroke", "#475569")
      .attr("stroke-opacity", 0.8)
      .selectAll("line")
      .data(links)
      .join("line");

    const node = svg
      .append("g")
      .attr("stroke", "#0f172a")
      .attr("stroke-width", 1.5)
      .selectAll("circle")
      .data(nodes)
      .join("circle")
      .attr("r", 10)
      .attr("fill", "#22c55e")
      .call(
        d3
          .drag<SVGCircleElement, Node>()
          .on("start", (event, d) => {
            if (!event.active) simulation.alphaTarget(0.3).restart();
            d.fx = d.x;
            d.fy = d.y;
          })
          .on("drag", (event, d) => {
            // event.x / event.y are already in **local** SVG coords
            d.fx = event.x;
            d.fy = event.y;
          })
          .on("end", (event, d) => {
            if (!event.active) simulation.alphaTarget(0);
            d.fx = null;
            d.fy = null;
          })
      );

    const label = svg
      .append("g")
      .attr("font-size", 10)
      .attr("fill", "#e5e7eb")
      .selectAll("text")
      .data(nodes)
      .join("text")
      .attr("text-anchor", "middle")
      .attr("dy", -14)
      .text((d) => d.id);

    const simulation = d3
      .forceSimulation(nodes)
      .force(
        "link",
        d3
          .forceLink<Node, Link>(links)
          .id((d) => d.id)
          .distance(80)
      )
      .force("charge", d3.forceManyBody().strength(-180))
      .force("center", d3.forceCenter(size.width / 2, size.height / 2))
      .on("tick", () => {
        link
          .attr("x1", (d: any) => d.source.x)
          .attr("y1", (d: any) => d.source.y)
          .attr("x2", (d: any) => d.target.x)
          .attr("y2", (d: any) => d.target.y);

        node.attr("cx", (d) => d.x as number).attr("cy", (d) => d.y as number);
        label.attr("x", (d) => d.x as number).attr("y", (d) => d.y as number);
      });

    return () => {
      simulation.stop();
    };
  }, [size.width, size.height]);

  return (
    <div ref={containerRef} className="h-full w-full">
      <svg ref={svgRef} className="h-full w-full"></svg>
    </div>
  );
}
